:root {
  --primary: #232f3e;
  --secondary: #f3a847;
  --tertiary: #b12704;
  /* --light: #fafafa; */
  --dark: #272f32;
  --error: rgb(228, 46, 1);
  --spacing-one: calc(1rem * 0.5);
  --spacing-two: calc(1rem);
}

* {
  box-sizing: border-box;
}

html,
body {
  min-height: 100%;
}
/* header styling */
.header {
  display: flex;
  justify-content: space-between;
  background-color: rgba(255, 255, 255, 0.5);
  font-family: "Pathway Gothic One", sans-serif;
  font-size: 1.5rem;
  border-bottom: 1px solid lightgray;
}
.header-logo {
  font-family: "Pathway Gothic One", sans-serif;
  padding: 7px 7px;
  color: black;
}
.nav-items {
  margin-top: 5%;
  color: #272f32;
  border: 1px solid rgb(216, 215, 215);
  border-radius: 20px;
  padding: 5px 5px;
  transition: 300ms;
}
.nav-items:hover {
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

/* header styling  ends*/

body {
  margin: 0;
  padding: 0;
  font-family: "Karla", sans-serif;
  font-weight: 400;
  line-height: 1.5;
  font-size: 16px;
  color: var(--dark);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Rubik", sans-serif;
  font-weight: 500;
  margin: 5px 0;
  line-height: 1.25;
}

h1 {
  font-size: 2.25rem;
}

h2 {
  font-size: 2rem;
}

h3 {
  font-size: 1.75rem;
}

h4 {
  font-size: 1.5rem;
}

h5 {
  font-size: 1.25rem;
}

h6 {
  font-size: 1rem;
}

p {
  font-size: 1.1rem;
  margin-top: 0;
}

a {
  color: var(--tertiary);
  text-decoration: none;
  font-weight: bold;
}

/* a:hover{
    color: #826aed;
    transition: .3s;
  }
   */

header {
  background: var(--primary);
}

header a {
  color: var(--light);
}

button {
  font-size: 1rem;
  text-decoration: none;
  padding: 0.3rem 1rem;
  border-radius: 0.5rem;
  border: none;
  outline: none;
  cursor: pointer;
  margin: 0 1px;
  background-color: var(--secondary);
}

button:hover {
  opacity: 0.8;
}

button[disabled] {
  cursor: default;
  opacity: 0.8;
}

img {
  max-width: 100%;
}

span[role="img"] {
  cursor: default;
}

form {
  width: 50%;
}
.signup-container {
  display: flex;
  flex-direction: row;
}
.signup-form {
  margin: auto;
}
.not-a-user,
.already-a-user {
  font-weight: 500;
  color: #272f32;
}
.not-a-user > span {
  color: #826aed;
}
.already-a-user > span {
  color: #826aed;
}
.signup-form > h2 {
  margin-top: 5%;
}
.login-right {
  width: 45%;
  margin-top: 5%;
}
.login-right-photo {
  width: 100%;
  height: 100%;
}
.login-or-sign-up-link {
  padding: 3px 8px 3px 8px;
  border-radius: 15px;
}
.login-or-sign-up-link:hover {
  transition: 0.2s;
  border: 1px solid #826aed;

  background-color: rgb(128, 79, 234, 0.1);
}
.FaChevronLeft {
  color: black;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
  padding: 0.3rem;
  border-radius: 5px;
}
.FaChevronLeft:hover {
  box-shadow: rgba(0, 0, 0, 0.05) 0px 6px 24px 0px;
  transition: 0.4s;
}
.go-back-button {
  border: 1px solid salmon;
  position: absolute;
  bottom: 5%;
  display: none;
}

.submit-button {
  border-radius: 0px;
  width: 70%;
  padding: 0.8rem;
  background: #000;
  color: #fff;
  z-index: 1;
  font-weight: bolder;
}

input {
  font-size: 1rem;
  padding: 0.6rem;
  width: 70%;
  outline: none;
  transition: box-shadow 0.2s;
}

.float-label {
  display: flex;
  flex-direction: column;
  position: relative;
}
.float-label .Active {
  transform: translate(0, 12px) scale(0.75);
  padding-bottom: 5px;
}

.float-label input {
  height: 56px;
  padding: 14px 16px 0 10px;
  outline: 0;
  border: 1px solid black;

  background: #fff;
  font-family: "Pathway Gothic One", sans-serif;
  font-size: 1rem;
}

.float-label label {
  font-size: 1rem;
  font-family: "Pathway Gothic One", sans-serif;
  padding: 0px 15px;
  color: black;
  pointer-events: none;
  position: absolute;
  transform: translate(0, 26px) scale(1);
  transform-origin: top left;
  transition: all 0.2s ease-out;
}

.float-label:focus-within label {
  transform: translate(0, 12px) scale(0.75);
}

#details-username {
  font-size: 1rem;
}
.details-body-container {
  margin-top: 10%;
  display: flex;
  justify-content: center;
  font-family: "Nunito Sans", sans-serif;
  /* justify-items: center;
  align-content: center; */
  /* width: 60%; */
}
.back-to-products {
  color: black;
  font-family: "Nunito Sans", sans-serif;
}
/* details styling */
.details-body {
  display: flex;
  flex-direction: column;
  width: 55%;
}
.details-body h2 {
  font-size: 2rem;
  font-family: "Nunito Sans", sans-serif;
}
.details-body-img-contaier {
  width: 100%;
  border-radius: 15px;
  height: 510px;
}
.details-body-img-contaier img {
  width: 100%;
  border-radius: 15px;
  height: 100%;
  border-radius: 5%;
}
.details-body-img-parent {
  box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
  border-radius: 15px;
  display: flex;
  justify-content: center;
}
.details-description {
  font-family: "Nunito Sans", sans-serif;
  font-size: 1.2rem;
}
.details-price-container {
  display: flex;
  justify-content: space-between;
  margin-bottom: 5%;
}
.details-name-username-container {
  margin-top: 3%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.details-price {
  font-weight: 600;
  font-size: 1.5rem;
  font-family: "Nunito Sans", sans-serif;
}

.no-reviews-yet {
  text-align: center;
}
/* details js ended */

/* profile js */
.profile-component-h2 {
  text-align: center;
  font-family: "Nunito Sans", sans-serif;
  margin-top: 3%;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
  background-color: rgb(242, 242, 242);
  border-radius: 15px;
  padding: 10px;
  font-size: medium;
}
.add-to-cart-btn,
.delete-from-cart-btn {
  font-family: "Nunito Sans", sans-serif;
  color: white;
  border-radius: 10px;
  padding: 7px 20px 7px 20px;
  background-color: rgb(128, 79, 234);
}
.container {
  width: 100%;
  /* max-width: 1200px; */
  margin-left: auto;
  margin-right: auto;
}

.flex-row {
  display: flex;
  flex-wrap: wrap;
}

.justify-center {
  justify-content: center;
}

.space-between {
  justify-content: space-between;
}

.flex-end {
  justify-content: flex-end;
}

.mx-1 {
  margin-right: var(--spacing-one);
  margin-left: var(--spacing-one);
}

.mx-2 {
  margin-right: var(--spacing-two);
  margin-left: var(--spacing-two);
}

.my-1 {
  margin-top: var(--spacing-one);
  margin-bottom: var(--spacing-one);
}

.my-2 {
  margin-top: var(--spacing-two);
  margin-bottom: var(--spacing-two);
}

.px-1 {
  padding-right: var(--spacing-one);
  padding-left: var(--spacing-one);
}

.px-2 {
  padding-right: var(--spacing-two);
  padding-left: var(--spacing-two);
}

.py-1 {
  padding-top: var(--spacing-one);
  padding-bottom: var(--spacing-one);
}

.py-2 {
  padding-top: var(--spacing-two);
  padding-bottom: var(--spacing-two);
}

.product-img {
  border-radius: 10px;
  width: 100%;
  height: 300px;
}
.error-text {
  color: var(--error);
}

.page-not-found-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.page-not-found-container img {
  margin-top: 5%;
  width: 40%;
}
.page-not-found-container h1 {
  margin-top: 5%;
  color: #272f32;
}
.reviewphoto-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 5%;
  margin-bottom: 5%;
  opacity: 0.5;
}
.reviewphoto {
  width: 300px;
}
.reviewphoto-container p {
  font-size: 1.3rem;
}

@media screen and (max-width: 768px) {
  .nav-items {
    display: none;
  }
  .header {
    justify-content: center;
  }
  .details-body-container {
    width: 100%;
  }
  .details-description p {
    font-size: 2rem;
  }
  .details-body {
    display: flex;
    flex-direction: column;
    width: 95%;
  }
  .reviewphoto-container {
    margin-top: 7%;
  }
  .signup-container {
    display: flex;
    flex-direction: column-reverse;
  }
  .signup-form,
  .login-right,
  input,
  .submit-button {
    width: 98%;
    margin: auto;
  }
  .signup-form > h2 {
    text-align: center;
    font-weight: bolder;
  }
  .login-right {
    display: flex;
    justify-content: center;
  }
  .login-right-photo {
    height: 60%;
    width: 60%;
  }
  .submit-button {
    padding: 0.7rem;
    background-color: black;
    color: white;
    font-weight: bolder;
  }

  .card {
    width: 100%;
    margin-top: 20%;
    margin-bottom: 7%;
    height: 300px;
  }
}
